body {
  background: #f1f1f1;
  padding: 50px;
}

div#crypto-container {
  background: white;
  width: 70%;
  margin: 0 auto 4px auto;
  padding: 1em;
  box-shadow: 1px 1px 0 lightgrey;
}

span.left {
  font-weight: bold;
}

span.right {
  float: right;
}

div#buttons {
  margin-left: 33%;
  margin-bottom: 5%;
}

div.select p {
  margin-left: 36.5%;
  margin-top: 10%;
  color: rgb(141, 139, 139);
  font-size: 15px;
}

button {
  font-size: 20px;
}

nav {
  display: flex;
  justify-content: space-around;
}

li {
  list-style: none;
}

li#logo {
  font-size: 30px;
  font-family: Roboto, sans-serif;
  font-style: italic;
  color: rgb(10, 10, 145);
}

li a {
  text-decoration: none;
  color: rgb(97, 92, 92);
}

li a:hover {
  color: black;
}
